'use strict';

const body = document.querySelector('body');
const logo = document.querySelector('.logo');

const resolver1 = (resolve) => {
  logo.addEventListener('click', () => {
    resolve();
  });
};

const resolver2 = (resolve, reject) => {
  setTimeout(() => {
    reject();
  }, 3000);
};

const promise1 = new Promise(resolver1);
const promise2 = new Promise(resolver2);

promise1
  .then(result => {
    const message = document.createElement('div');

    message.className = 'message';
    message.innerText = 'Promise was resolved!';
    body.append(message);
  });

promise2
  .catch(result => {
    const message = document.createElement('div');

    message.className = 'error-message';
    message.innerText = 'Promise was rejected!';
    body.append(message);
  });
